import NewItems from './NewItems';
import AllItems from './AllItems';
import Dates from './Date';
import Nav from './nav/Nav';

import { Link } from 'react-router-dom';

const Main = () => {
    return(
        <div>
        <Nav />
         <div className='header'>
            <h5><Link to='/user'>Profile</Link></h5>
            <Dates/>
         </div>
         <h1>Shopping List</h1>
         <NewItems />
         <AllItems />
        </div>
    ) 
}
export default Main;