import { useRef } from "react";


const Item = ({item, onRemove}, props) => {

    const styleBox ={
        backgroundColor: 'rgba(199, 134, 179, 0.8)',
        with: '100%'
    }
    const styleQuantity={
        visibility: 'visible'
    }

    const boxRef = useRef();
    const quantityRef = useRef();

    function onTouchStart(){
        boxRef.current.style.backgroundColor= 'black';
    }
    function onTouchMove() {
        boxRef.current.style.width= '10rem';
        quantityRef.current.style.visibility= 'hidden';
        
    }
    function onTouchEnd(){
        
        
        
    }

    return(
        
        
            <div className='box' style={styleBox} ref={boxRef} onTouchStart={onTouchStart} onTouchMove={onTouchMove} onTouchEnd={() => {onRemove(item.id); onTouchEnd(); } } onClick={() => onRemove(item.id)}>
            <li><p className=''>{item.newitem}</p></li>
            <li style={styleQuantity} ref={quantityRef}><p className='quantity '>{item.quantity}</p></li>
            {/* <button onClick={() => onRemove(item.id)}>X</button> */}
           
            
        </div>  
       
        
    )
}

export default Item;