
import Item from './Item';


const ItemList = ( props) => {
    
    


    return(
        <div >
          
            {props.items.map((item) =>(
              
                <Item key={item.id} id={item.id} item={item} newitem={item.newitem} quantity={item.quantity}  onRemove= {props.onRemove} /> 
              
           
            )) } 
           
          

            
        </div>
    )
}

export default ItemList;